import React, { useState, useEffect } from 'react';
import { Button, Typography, Container, Box, CircularProgress, Grid } from '@mui/material';  //  MUI 
import axios from 'axios';

const CLIENT_ID = 'Ov23lia6FZ5MumBJil3m';  //  GitHub  Client ID
const REDIRECT_URI = 'http://localhost:3000/callback'; // GitHub  URL

const BUTTON_BLACK = 'black';  // 

const LoginPage = () => {
  const [user, setUser] = useState(null);  // 
  const [loading, setLoading] = useState(false);  // 

  // Step 1:  GitHub  OAuth 
  const handleLogin = () => {
    const githubAuthUrl = `https://github.com/login/oauth/authorize?client_id=${CLIENT_ID}&redirect_uri=${REDIRECT_URI}&scope=user`;
    window.location.href = githubAuthUrl;  //  GitHub 
  };

  // Step 2:  GitHub OAuth 
  const handleOAuthCallback = async () => {
    const urlParams = new URLSearchParams(window.location.search);  //  URL 
    const code = urlParams.get('code');  // 

    if (code) {
      try {
        setLoading(true);  // 
        // Step 1:  access token 
        const response = await axios.get(`http://localhost:5000/auth/github?code=${code}`);
        
        const userInfo = response.data.user;  // 

        setUser(userInfo);  // 
      } catch (error) {
        console.error('GitHub OAuth :', error);
        setLoading(false);  // 
      }
    }
  };

  //  URL  GitHub  code 
  useEffect(() => {
    if (window.location.search.includes('code=')) {
      handleOAuthCallback();  //  GitHub 
    }
  }, []);

  return (
    <Container component="main" maxWidth="xs" sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
      {/*  */}
      <Typography 
        variant="h3" 
        component="h2" 
        sx={{ 
          fontWeight: 'bold', 
          letterSpacing: 0.5, 
          marginBottom: 3, 
          textAlign: 'center', 
          position: 'absolute',  // 
          top: '25%', 
          left: '50%', 
          transform: 'translate(-50%, -50%)', // 
          width: '100%',  // 
          color: 'rgba(0, 0, 0, 0.8)',  // 
          wordBreak: 'break-word',  // 
        }}
      >
        Database CI/CD and <br />Security <span style={{ color: 'rgb(86, 71, 235)' }}>at Scale</span>
      </Typography>

      {/*  */}
      {!user ? (
				
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',  // 
            alignItems: 'center',
            justifyContent: 'center',
            width: '100%',
          }}
        >
          <Typography variant="h4" component="h1" gutterBottom sx={{ fontWeight: 'bold', letterSpacing: 0.5, marginTop: 5 }}>
            Login with GitHub
          </Typography>

          <Button 
            fullWidth 
            variant="contained" 
            onClick={handleLogin} 
            sx={{ 
              marginTop: 5,  
              marginBottom: 5,  
              backgroundColor: BUTTON_BLACK,  
              '&:hover': {
                backgroundColor: 'gray',  //  hover 
              },
              borderRadius: '50px',  // 
              padding: '12px 50px',  // 
              fontWeight: 'bold',  // 
              letterSpacing: 0.5,  // 
              maxWidth: '300px',  // 
            }}
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} color="inherit" /> : 'Login'}
          </Button>
        </Box>
      ) : (
        //  Box
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',  // 
            alignItems: 'center',
            justifyContent: 'center',
            padding: 5,
            borderRadius: 4,
            boxShadow: 3,
            backgroundColor: 'white',
            width: '100%',
          }}
        >
          <Grid container direction="column" alignItems="center" spacing={2}>
            <Grid item>
              <Typography variant="h6" sx={{ fontWeight: 'bold', letterSpacing: 0.5 }}>Welcome, {user.login}</Typography>
            </Grid>
            <Grid item>
              <img 
                src={user.avatar_url} 
                alt="Avatar" 
                style={{ width: 100, borderRadius: '50%' }} 
              />
            </Grid>
            <Grid item>
              <Typography variant="body1" sx={{ fontWeight: 'bold', letterSpacing: 0.5 }}>GitHub Username: {user.login}</Typography>
            </Grid>
          </Grid>
        </Box>
      )}
    </Container>
  );
};

export default LoginPage;
